// tag标签
import uniTag from "@/components/uni-ui/uni-tag/uni-tag.vue"
import kList from '@/components/k-ui/k-user-list/k-list.vue'
import uniLink from "@/components/uni-ui/uni-link/uni-link.vue"
import {
	mapState
} from 'vuex';
export default {
	props:{
		list: {
			
		},
		poster: {
			
		},
		isCollect: {
			type: Boolean,
			default: false
		},
		isPreview: {
			type: Boolean,
			default: false
		}
	},
	components: {
		uniTag,
		kList,
		uniLink
	},
	computed: {
		...mapState(['hasLogin', 'userInfo']),
	},
	data() {
		return {
			// 数据
			datas:[],
			// 其他
			// isCollect: false
		}
	},
	methods: {
		isUnknowTime(date){
			const flag = this.$common.isUnknowTime(date)
			if(flag) return "待定"
			return date
		},
		trigger(e) {
			let other = this.content.map((d, i) => {
				d.active = i == e.index
			})

			uni.showToast({
				title: '选择了' + this.content[e.index].text
			})
		},
		type(i) {
			if(i == 0)
				return 'success'
			else if(i == 1)
				return 'warning'
			else 
				return 'primary'
		},
		userDetail(id){
			// console.log(e)
			uni.navigateTo({
				url: '/pages/person/person-detail?id=' + id
			})
		},
		collect(id){
			this.$emit("collect", id)
		},
		participate(){
			this.$emit("participate")
		},
		editGame(){
			this.$emit('editGame')
		},
		deleteGame(){
			this.$emit('deleteGame')
		},
		isEnd(e){
			return this.$api.isEnd(e)
		}
	}
}
